.js-btn {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

.btn {
  display: inline-block;
  font-family: $buttonFont;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
  color: #fff;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border: none;
  outline: none;
  transition: all .3s;
  user-select: none;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  &--std {
    padding: 12px 35px 14px;
    color: #f1f1f1;
    font-family: Verdana, sans-serif;
    font-size: 32px;
    background-color: #00a651;
    border-radius: 5px;
    box-shadow: 2.7px 7.5px 17px 4px rgba(0, 0, 0, 0.48);
    @include r(767) {
      font-size: 24px;
    }
    &:hover {
      box-shadow: none;
    }
  }//btn--std
}//btn