html, body {
  height: 100%;
}

body {
  min-height: 600px;
}

.b-page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
  height: 100%;
  background: url($img + 'body-bg.jpg') no-repeat center center;
  background-size: cover;
  @include r(959) {
    max-height: none;
    height: auto;
  }
}

.b-header {
  flex-shrink: 0;
  & > .l {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include r(599) {
      flex-direction: column;
    }
  }
}

.b-logo {
  display: flex;
  align-items: center;
  flex-shrink: 1;
  & > img {
    margin-right: 10px;
    display: block;
    transform: translateY(2px);
  }
  &__text {
    max-width: 390px;
    color: #f1f1f1;
    font-family: Verdana, sans-serif;
    font-size: 20px;
    font-weight: 400;
    @include r(1169) {
      font-size: 14px;
      max-width: 200px;
    }
    @include r(767) {
      display: none;
    }
  }
}

.b-contacts {
  &__item {
    display: table;
    margin: 10px 0;
    margin-left: auto;
    color: #fff;
    font-family: Cambria;
    font-size: 20px;
    font-weight: 700;
    text-decoration: none;
    @include r(959) {
      font-size: 17px;
    }
    @include r(767) {
      margin: 10px auto;
    }
    & > svg {
      display: inline-block;
      vertical-align: middle;
      margin-right: 7px;
      fill: #00a651;
    }
  }
}

.b-content {
  width: 100%;
  flex-grow: 1;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
}

.b-title {
  max-width: 100%;
  margin-bottom: 40px;
  font-family: Verdana, sans-serif;
  font-size: 43px;
  color: #f1f1f1;
  @include r(1169) {
    font-size: 28px;
  }
  @include r(599) {
    font-size: 18px;
    margin-bottom: 20px;
  }
  @media screen and (max-height: 750px) {
    font-size: 28px;
  }
  span {
    color: #00a651;
  }
}

.b-video {
  display: block;
  position: relative;
  font-size: 12px;
  width: .875em;
  height: 1.125em;
  @include r(767) {
    font-size: 9px;
  }
  &:hover {
    cursor: pointer;
  }
  & > svg {
    display: block;
    margin-top: -3px;
    font-size: 1.4em;
    color: transparent;
    transition: color .3s;   
    fill: currentColor; 
    @include r(767) {
      margin-top: -4px;
      margin-left: -2px;
    }
  }
  &__circle {
    display: block;
    position: absolute;
    top: -1.9375em;
    left: -2.125em;
    width: 4.8125em;
    height: 4.8125em;
    border-radius: 100%;
    border: 0.125em solid rgba(255,255,255,.4);
    border-left-color: rgba(255,255,255,.1);
    border-top-color: rgba(255,255,255,.1);
    transform: rotate(-45deg);
    animation: loaderleft 10s linear infinite;
    @include r(767) {
      animation: none;
    }
    @at-root {
      @keyframes loaderleft {
        0% { transform: rotate(360deg) }
        50% { transform: rotate(180deg) }
        100% { transform: rotate(0) }
      }
    }
  }
  &__circle-inner {
    display: block;
    position: absolute;
    right: -3.3125em;
    top: -3.5em;
    border: 0.0625em solid rgba(255,255,255,.2);
    width: 7.8125em;
    height: 7.8125em;
    border-radius: 100%
  }
  &__circle-outer {
    margin-left: 9.0625em;
    margin-top: -7.5em;
    border: 0.5em solid $mainColor;
    width: 8.625em;
    height: 8.625em;
    border-radius: 100%;
    border-left-color: transparent;
    border-top-color: transparent;
    transform: rotate(120deg);
    position: absolute;
    right: -3.75em;
    top: 3.625em;
    animation: spinloader 8s linear infinite;
    @include r(767) {
      animation: none;
    }
    @at-root {
      @keyframes spinloader {
        0% { transform: rotate(0) }
        50% { transform: rotate(180deg) }
        100% { transform: rotate(360deg) }
      }
    }
  }
}//b-video

.b-mainVideo {
  width: 790px;
  max-height: 430px;
  min-height: 200px;
  max-width: 100%;
  position: relative;
  flex-grow: 1;
  border: 10px solid #fff;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  &:hover {
    cursor: pointer;
  }
  &.hide-bg {
    &:hover {
      cursor: default;
    }
    .b-video {
      display: none;
    }
  }
  @include r(1400) {
    width: 600px;
    border: 5px solid #fff;
  }
  @include r(959) {
    flex-grow: 0;
    width: 100%;
    max-width: 400px;
    height: auto;
    &::after {
      content: '';
      padding-bottom: 54%;
      display: block;
    }
  }
  img, iframe {
    position: absolute;
    left: 0; right: 0; top: 0; bottom: 0;
    max-width: 100%;
    max-height: 100%;
    margin: auto;
    display: block;
  }
  iframe {
    width: 100%;
    height: 100%;
    border: none;
  }
  .b-video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.b-videoTip {
  position: relative;
  display: table;
  margin: 30px auto;
  color: #f1f1f1;
  font-family: Verdana, sans-serif;
  font-size: 21px;
  font-weight: 400;
  @include r(479) {
    font-size: 12px;
    margin: 15px auto;
  }
  &::after {
    content: '';
    position: absolute;
    left: 100%;
    bottom: 5px;
    width: 76px;
    height: 42px;
    transform: translateX(10px);
    background: url($img + 'arrow.png') no-repeat;
    @include r(1400) {
      content: none;
    }
  }
}

.b-footer {
  flex-shrink: 0;
  padding: 7px 0;
  color: #f1f1f1;
  font-family: Cambria;
  font-size: 18px;
  font-weight: 700;
  background-color: rgba(0, 0, 0, 0.5);
  @include r(767) {
    font-size: 15px;
  }
  &__text {
    text-align: center;
  }
}