.l {
	max-width: 1200px;
	margin: 0 auto;
	padding-left: 15px;
	padding-right: 15px;
	&--wide {
		max-width: 1700px;
	}
}

.l-grid {
	font-size: 0;
	margin: 0 -15px;
}

[class*="l-col-"] {
	font-size: 1rem;
	padding: 0 15px;
	vertical-align: top;
	display: inline-block;
}

.l-col-25 {
	width: 25%;
}

.l-col-33 {
	width: 33.33333%;
}

.l-col-50 {
	width: 50%;
}

.is-hidden {
	display: none;
}